import { graphql } from "gatsby"
import Layout from "src/components/Layout"
import Posts from "src/components/Posts"
import { React } from "src/utils/view"

import { redirections } from "../../redirections"
import Loading from "../components/Loading"

type Props = any

const IndexPage = ({ data }: Props) => {
  let redirectFound = false
  if (typeof window !== "undefined" && window.document.location) {
    const l = window.document.location
    const possiblePermalink = `${l.pathname}${l.search}`
    const redirect = redirections.find(r => r.fromPath === possiblePermalink)
    if (redirect) {
      redirectFound = true
      window.document.location.href = redirect.toPath
    }
  }
  return !redirectFound ? (
    <Layout>
      <Posts data={data} />
    </Layout>
  ) : (
    <Loading>Redirecting to post...</Loading>
  )
}

export default IndexPage

export const query = graphql`
  query {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date] }
      filter: {frontmatter: { draft: { ne: true } }}
    ) {
      edges {
        node {
          id
          timeToRead
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            url
            title
            author
            draft
            categories
            tags
            type
            version
            video
          }
        }
      }
    }
  }
`
